<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>显示的内容为：{{this.$store.state.Car.age}}</p>
    <p>{{showAlert()}}</p>
    <p>{{this.$store.state.Car.name}}</p>
    <button v-on:click="changeCurrentValue">修改</button>
  </div>
</template>

<script>
  export default {
    name: 'Home',
    data () {
      return {
        msg: '主页'
      }
    },

    methods:{
      showAlert(){
        if (this.$store.state.count % 2 === 0) {
          return 'True';
        } else {
          return 'False';
        }

      },

      changeCurrentValue(){
        // this.$store.commit('changeValue', '你好不好');
        this.$store.commit('Car/changeName', '哈哈');
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1{
    font-weight: normal;
  }
</style>



