const state = {
  name:'张三',
  age:18
}

const mutations = {
  changeName(state, newName) {
    state.name = newName;
  },

  changeAge(state, newAge) {
    state.age = newAge;
  }
}

const actions = {
  changePersonAge({state, commit}, newAge) {
      commit('changeAge', newAge);
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



