<template>
  <div id="app">
    <img src="./assets/logo.png">
    <p>{{this.$store.state.count}}</p>
    <button v-on:click="updateValue">点我</button>
    <ul style="text-align: left">
      <li><router-link to="/">首页</router-link></li>
      <li><router-link to="/mine">我的</router-link></li>
      <li><router-link to="/hello">欢迎</router-link></li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import {mapMutations} from 'vuex'
import { mapActions } from 'vuex'

export default {
  name: 'App',
  components: {
  },

  methods:{
    ...mapMutations([
      'increment'
    ]),

    ...mapActions([
      'incrementValue'
    ]),

    updateValue(){
      // this.increment();
      // this.$store.dispatch('incrementValue');
      // this.incrementValue();
      this.$store.dispatch('Car/changePersonAge', 30);
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



