<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>显示的内容为：{{this.$store.state.count}}</p>
  </div>
</template>

<script>
  export default {
    name: 'Home',
    data () {
      return {
        msg: '我的'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1{
    font-weight: normal;
    background-color: aqua;
    color: brown;
  }
</style>



